import {AfterViewInit, Component, ElementRef, OnInit, Query, ViewChild, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Params, Router} from "@angular/router";
import {ServerService} from "../../../services/server-service";
import {Location} from "@angular/common";
import {NgbTabset} from "@ng-bootstrap/ng-bootstrap";
import {PartyService} from "../../../services/party.service";
import {Subscription} from "rxjs/Subscription";

@Component({
  selector: 'app-party',
  templateUrl: './party.component.html',
  styleUrls: ['./party.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers:[PartyService]
})
export class PartyComponent implements  AfterViewInit, OnInit {
  partyBills: any = null;
  partyInvoices: any = null;
  data: any;
  partyId:number;
  partyServiceSubscription:Subscription;

  constructor(private activatedRoute: ActivatedRoute,
              private router: Router,
              private serverService: ServerService,
              private location: Location,
              private partyService: PartyService) {
  }
  ngOnInit() {

  }

  ngAfterViewInit() {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params['id']);
      this.partyId = params['id'];
      this.partyServiceSubscription = this.partyService.partyIdSourceObservable.subscribe((id) => {
        this.partyId = id;
        this.makeDataRequest();
      });
      this.partyService.announcePartyId(this.partyId);
    }, (error) => console.log(error));

  }

  makeDataRequest() {

    this.serverService.getRequestByPartyId(this.partyId, null, {params: 'projection=partyProjection'})
      .subscribe((data: any) => {
        this.data = data;

      }, (error) => {
        console.log(error);
      });
    this.serverService.getRequestByPartyId(this.partyId, '/bills')
      .subscribe((bills: any) => {
        this.partyBills = bills._embedded.bills;

      }, (error) => {
        console.log(error);
      });
    this.serverService.getRequestByPartyId(this.partyId, '/invoices')
      .subscribe((invoices: any) => {
        if (invoices._embedded) {
          this.partyInvoices = invoices._embedded.invoices;
        }

      }, (error) => {
        console.log(error);
      });
  }

  onTabSelected() {
    this.partyService.announcePartyId(this.partyId);
  }
}



