import {ChangeDetectorRef, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "../../../../services/server-service";
import {GridOptions} from "ag-grid";
import {Subscription} from "rxjs/Subscription";
import {NavBarService} from "../../../../services/nav-bar.service";
import {PartyService} from "../../../../services/party.service";

@Component({
  selector: 'app-party-invoices',
  templateUrl: './party-invoices.component.html',
  styleUrls: ['./party-invoices.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartyInvoicesComponent implements OnInit {
  paramsReceived: number = null;
  partyInvoices: any;
  gridOptions: GridOptions;
  rowData: any[];
  navBarSubscription: Subscription;
  partyServiceSubscription: Subscription;
  googleMapsClient: any;


  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private partyService:PartyService) {

  }

  ngOnInit() {
    this.partyServiceSubscription = this.partyService.partyIdSourceObservable.subscribe((id) => {
      console.log(id);
      console.log(id['id']);
      this.paramsReceived = id;
      this.serverService.getRequestByPartyId(this.paramsReceived, '/invoices')
        .subscribe(
          (partyInvoices: any) => {
            console.log(partyInvoices);
            if (partyInvoices._embedded) {
            this.rowData = partyInvoices._embedded.invoices;
            }
          },
          (error) => {
            console.log(error)
          });
    }, (error) => console.log(error));
    this.gridOptions = {
      context: {
        componentParent: this
      },
      rowData: this.rowData,
      columnDefs: this.columnDefs
    }

    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      console.log("sizeColumnsToFit");
      this.gridOptions.api.sizeColumnsToFit();
    });
  }

  newCustomer() {

  }

  columnDefs = [
    {
      headerName: "Invoice id",
      field: 'description',
      cellClass: 'first-column'
    },
    {
      headerName: "Job id",
      field: "physical_address",
      cellClass: 'second-column'
    },
    {
      headerName: "Invoice date",
      field: "invoice_dt",
      cellClass: 'third-column'
    },
    {
      headerName: "Due date",
      field: "invoice_due_date",
      cellClass: 'third-column'
    },
    {
      headerName: "Total amount",
      field: "total_amount",
      cellClass: 'third-column'
    }
  ];


  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event.data);
    let href = $event.data._links.self.href;
    let index = (href.lastIndexOf('/') + 1);
    let id = +(href.substr(index));
    console.log(id);
    // this.router.navigate(['party', id, 'invoices'])
    //   .then((value) => {
    //     console.log(value);
    //   }).catch((error) => {
    //   console.log(error)
    // });
  }

  onCellDoubleClicked($event) {
    console.log($event.data._links.self);
  }

  ngAfterViewInit() {
    this.gridOptions.api.sizeColumnsToFit();
  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
    this.partyServiceSubscription.unsubscribe();
    this.partyService.announcePartyIdBeforeDestroy(this.paramsReceived);
  }
}



