import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {ServerService} from "../../../../services/server-service";
import {PartyService} from "../../../../services/party.service";
import {Subscription} from "rxjs/Subscription";

@Component({
  selector: 'app-party-jobs',
  templateUrl: './party-jobs.component.html',
  styleUrls: ['./party-jobs.component.scss']
})
export class PartyJobsComponent implements OnInit, OnDestroy {

  paramsReceived:number = null;
  partyJobs:any;
  partyServiceSubscription:Subscription;

  constructor(private activatedRoute: ActivatedRoute, private serverService:ServerService,
              private partyService:PartyService) { }

  ngOnInit() {
    this.partyServiceSubscription= this.partyService.partyIdSourceObservable.subscribe((id) => {
      this.paramsReceived = id;
      this.serverService.getRequestByPartyId(this.paramsReceived, '/jobs')
        .subscribe(
          (partyJobs:any)=>{
            console.log(partyJobs);
            this.partyJobs = partyJobs._embedded.jobs;
          },
          (error)=>{console.log(error)});
    }, (error) => console.log(error));
  }

  ngOnDestroy() {
    this.partyServiceSubscription.unsubscribe();
    this.partyService.announcePartyIdBeforeDestroy(this.paramsReceived);
  }
}



