import {RouterModule, Routes} from "@angular/router";
import {NgModule} from "@angular/core";
import {PartiesComponent} from "./components/parties/parties.component";
import {PartyComponent} from "./components/parties/party/party.component";
import {PartyInvoicesComponent} from "./components/parties/party/party-invoices/party-invoices.component";
import {PartyBillsComponent} from "./components/parties/party/party-bills/party-bills.component";
import {PartyJobsComponent} from "./components/parties/party/party-jobs/party-jobs.component";
import {PartyMainComponent} from "./components/parties/party/party-main/party-main.component";

export const routes: Routes = [
  {path: '', redirectTo: 'parties', pathMatch: 'full'},
  {path: 'parties', component: PartiesComponent},
  {path: 'party/:id', component:PartyComponent,
    children: [
      {path: '', redirectTo:'main', pathMatch:'full'},
      { path: 'main', component: PartyMainComponent},
      { path: 'jobs', component: PartyJobsComponent},
      { path: 'bills', component: PartyBillsComponent},
      { path: 'invoices', component: PartyInvoicesComponent},
    ]
  },
  {path: '**', redirectTo: 'parties', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}



