import { Injectable } from '@angular/core';
import {Subject} from "rxjs/Subject";

@Injectable()
export class PartyService {

  partyIdSource = new Subject<number>();

  partyIdSourceObservable = this.partyIdSource.asObservable();

  constructor() { }

  announcePartyId(id:number) {
    console.log(id);
    this.partyIdSource.next(id);
  }

  announcePartyIdBeforeDestroy(id:number) {
    setTimeout(()=>{
      this.partyIdSource.next(id);
    },100)
  }
}



