import {AfterViewInit, ChangeDetectorRef, Component, Input, OnDestroy, OnInit, ViewEncapsulation} from "@angular/core";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {Observable} from "rxjs/Observable";
import {ServerService} from "../../../../services/server-service";
import {Http, RequestOptions} from "@angular/http";
import {ActivatedRoute, Router} from "@angular/router";
import {PartyModel} from "../../../../models/party.model";
import {PartyService} from "../../../../services/party.service";
import {Subscription} from "rxjs/Subscription";
import {ModalDismissReasons, NgbModal} from "@ng-bootstrap/ng-bootstrap";


declare var google: any;

@Component({
  selector: 'app-party-main',
  templateUrl: './party-main.component.html',
  styleUrls: ['./party-main.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartyMainComponent implements OnInit, AfterViewInit, OnDestroy {
  party: any = null;
  usersComments: any = null;
  paramsReceived: number = null;
  signUpForm: FormGroup;
  googleAutocompleteService: any;
  detailFormActive = false;
  partyPhysicalAddress: string;
  partyMailingAddress: string;
  partyPhone: string;
  partyMobile: string;
  newComment: boolean = false;
  partyServiceSubscription: Subscription;
  closeResult: string;
  userCommentInput: string;

  private ADDRESS_PHYSICAL: string = "ADDRESS_PHYSICAL";
  private ADDRESS_MAILING: string = "ADDRESS_MAILING";
  private EDIT_CUSTOMER: string = "EDIT_CUSTOMER";
  private CREATE_CUSTOMER: string = "CREATE_CUSTOMER";

  formMode: string = null;

  autoSuggestionMode = null;

  addressFormArray = [
    {
      name: 'party-physical-address',
      title: 'Physical Address',
      type: this.ADDRESS_PHYSICAL,
      autoAddressesSuggesstion: [],
      addressSubscription: null,
      spinner: false
    },
    {
      name: 'party-mailing-address',
      title: 'Mailing Address',
      type: this.ADDRESS_MAILING,
      autoAddressesSuggesstion: [],
      addressSubscription: null,
      spinner: false
    }
  ];
  private googleService: any;


  constructor(private serverService: ServerService, private http: Http,
              private activatedRoute: ActivatedRoute, private changeDetectorRef: ChangeDetectorRef,
              private  router: Router, private partyService: PartyService,
              private ngbModal: NgbModal) {
    this.googleAutocompleteService = new google.maps.places.AutocompleteService();
    this.googleService = google;
  }

  ngOnInit() {
    this.createSignUpForm();
    this.partyServiceSubscription = this.partyService.partyIdSourceObservable.subscribe((id: number) => {
      ("it is called");
      this.paramsReceived = id;
      this.getPartyRequest();
      this.getUserComments();
    }, (error) => console.log(error));
    this.initAddressesSubscription(this.addressFormArray[0]);
    this.initAddressesSubscription(this.addressFormArray[1]);
  }

  ngAfterViewInit() {

  }

  getPartyRequest() {
    this.serverService.getRequestByPartyId(this.paramsReceived, null, {params: 'projection=partyProjection'})
      .subscribe((data: any) => {
        this.party = data;
        this.setSignUpForm();
        this.subscribeSignUpForm();
      }, (error) => {
        console.log(error);
      });
  }

  getUserComments() {
    this.serverService.getRequestByPartyId(this.paramsReceived, '/comments')
      .subscribe(
        (comments: any) => {
          this.usersComments = comments._embedded.comments;
        },
        (error) => {
          console.log(error);
        });
  }

  createSignUpForm() {
    this.signUpForm = new FormGroup({
      'userData': new FormGroup({
        'party-name': new FormControl(null, [Validators.required]),
        'party-contact-name': new FormControl(null, [Validators.required]),
        'party-phone': new FormControl(null, [Validators.required, this.validatePhone.bind(this)]),
        'party-mobile': new FormControl(null, [Validators.required, this.validateMobile.bind(this)]),
        'party-email': new FormControl(null, [Validators.required, Validators.email, this.validateEmail.bind(this)]),
        'party-physical-address': new FormControl(null, [Validators.required], this.validatePhysicalAddress.bind(this)),
        'party-mailing-address': new FormControl(null, [Validators.required], this.validateMailingAddress.bind(this)),
        'party-file-upload': new FormControl({value: '', disabled: true})
      })
    });
    console.log(this.signUpForm);
  }

  setSignUpForm() {
    this.signUpForm.setValue({
      'userData': {
        'party-name': this.party.name,
        'party-contact-name': this.party.contact_name,
        'party-phone': this.party.phone,
        'party-mobile': this.party.mobile,
        'party-email': this.party.email,
        'party-physical-address': this.party.physical_address,
        'party-mailing-address': this.party.mailing_address,
        'party-file-upload': ''
      }
    })
    ;
  }

  patchSignUpForm() {

  }

  onSubmit() {
    console.log(this.signUpForm);
    if (this.formMode == this.CREATE_CUSTOMER) {
      this.saveNewCustomer();
    }
    if (this.formMode == this.EDIT_CUSTOMER) {
      //  this.patchExistingCustomer();
      this.putExistingCustomer();
    }
  }

  patchExistingCustomer() {
    console.log("patchExistingCustomercalled");
    let party = new PartyModel();
    if (this.signUpForm.valid && this.signUpForm.touched) {
      if (this.signUpForm.get('userData.party-name').touched) {
        party.name = this.signUpForm.get('userData.party-name').value;
      }
      if (this.signUpForm.get('userData.party-email').touched) {
        party.email = this.signUpForm.get('userData.party-email').value;
      }
      if (this.signUpForm.get('userData.party-phone').touched) {
        party.phone = this.partyPhone;
      }
      if (this.signUpForm.get('userData.party-mobile').touched) {
        party.mobile = this.partyMobile;
      }

      if (this.signUpForm.get('userData.party-contact-name').touched) {
        party.contact_name = this.signUpForm.get('userData.party-contact-name').value;
      }
      if (this.signUpForm.get('userData.party-mailing-address').touched) {
        party.mailing_address = this.partyMailingAddress;
      }
      if (this.signUpForm.get('userData.party-physical-address').touched) {
        party.physical_address = this.partyPhysicalAddress;
      }
      this.serverService.patchParty(this.paramsReceived, party).subscribe((response) => {
        alert("Successfully Saved");
        this.detailFormActive = false;
        this.getPartyRequest();
        this.signUpForm.markAsUntouched();
        console.log(response);
      }, (error) => {
        alert("Something went wrong !. Couldn't save the form.");
        console.log(error);
      })
    }
    else {
      alert("The form is either not edited or it is not valid ! \n Please try again.")
    }

  }

  putExistingCustomer() {
    console.log("put0ExistingCustomerCalled");
    if (this.signUpForm.touched && this.signUpForm.valid) {
      let party = new PartyModel();
      party.name = this.signUpForm.get('userData.party-name').value;
      party.email = this.signUpForm.get('userData.party-email').value;
      party.phone = this.partyPhone;
      party.mobile = this.partyMobile;
      party.contact_name = this.signUpForm.get('userData.party-contact-name').value;
      party.mailing_address = this.partyMailingAddress;
      party.physical_address = this.partyPhysicalAddress;
      this.serverService.putParty(this.paramsReceived, party).subscribe((response) => {
        this.open("Successfully Saved");
        this.detailFormActive = false;
        this.getPartyRequest();
        this.signUpForm.markAsUntouched();
        console.log(response);
      }, (error) => {
        this.open("Something went wrong!. Couldn't save the form." + error.toString());
      })
    }
    else {
      this.open("The form is either not edited or it is not valid ! \n Please try again.")
    }
  }

  saveNewCustomer() {
    console.log("save new customer called");
    if (this.signUpForm.touched && this.signUpForm.valid) {
      let party = new PartyModel();
      party.name = this.signUpForm.get('userData.party-name').value;
      party.email = this.signUpForm.get('userData.party-email').value;
      party.phone = this.partyPhone;
      party.mobile = this.partyMobile;
      party.contact_name = this.signUpForm.get('userData.party-contact-name').value;
      party.mailing_address = this.partyMailingAddress;
      party.physical_address = this.partyPhysicalAddress;
      this.serverService.saveParty(party).subscribe((response) => {
        console.log(response.json());
        this.open('Succesfully saved');
        let href = response.json()._links.self.href;
        let index = (href.lastIndexOf('/') + 1);
        let id = +(href.substr(index));
        this.paramsReceived = id;
        this.detailFormActive = false;
        this.getPartyRequest();
        this.signUpForm.markAsUntouched();
        this.router.navigate(['party', this.paramsReceived])
          .then((value) => {
            console.log(value);
          }).catch((error) => {
          console.log(error)
        });
      }, (error) => {
        this.open("Something went wrong!. Couldn't save the form.");
        console.log(error);
      })
    }
    else {
      this.open("The form is either not edited or it is not valid ! \n Please try again.")
    }
  }

  fileChange(event) {
    let fileList: FileList = event.target.files;
    if (fileList.length > 0) {
      let file: File = fileList[0];
      let formData: FormData = new FormData();
      formData.append('uploadFile', file, file.name);
      let headers = new Headers();
      headers.append('Content-Type', 'multipart/form-data');
      headers.append('Accept', 'application/json');
      let options = new RequestOptions({headers: null});
      this.http.post(`apiEndpoint`, formData, options)
        .map(res => res.json())
        .catch(error => Observable.throw(error))
        .subscribe(
          data => console.log('success'),
          error => console.log(error)
        )
    }
  }

  createNewComment() {
    console.log("create new comment");
    this.newComment = true;
  }

  usersCommentFileUpload($event) {
    console.log("usersCommentFileUpload called");
  }

  editCustomer() {
    this.detailFormActive = true;
    this.formMode = this.EDIT_CUSTOMER;
  }

  dontSaveCustomer() {
    this.setSignUpForm();
    this.signUpForm.markAsUntouched();
    this.addressFormArray[0].autoAddressesSuggesstion.length = 0;
    this.addressFormArray[1].autoAddressesSuggesstion.length = 0;
    this.detailFormActive = false;
  }

  createCustomer() {
    this.signUpForm.reset();
    this.detailFormActive = true;
    this.formMode = this.CREATE_CUSTOMER;
  }

  validatePhone(control: FormControl): { [s: string]: boolean } {
    let regex = /^(0(2|3|7|8))?\d{8}$/;
    let phone: string = control.value;
    console.log(control);
    console.log(phone);

    if (phone) {
      if (!regex.test(phone.replace(/\(+/g, '').replace(/\)+/g, '').replace(/\s+/g, '').replace(/-+/g, ''))) {
        console.log("Phone number is invalid");
        return {'InvalidPhone': true}
      }
      else {
        this.partyPhone = phone.replace(/\(+/g, '').replace(/\)+/g, '').replace(/\s+/g, '').replace(/-+/g, '');
      }
    }
    return null
  }

  validateMobile(control: FormControl): { [s: string]: boolean } {

    let regex = /^04\d{8}$/;
    let mobile: string = control.value;
    if (mobile) {
      console.log(regex.exec(mobile.replace(/\(+/g, '').replace(/\)+/g, '').replace(/\s+/g, '').replace(/-+/g, '')));
      if (!regex.test(mobile.replace(/\(+/g, '').replace(/\)+/g, '').replace(/\s+/g, '').replace(/-+/g, ''))) {
        console.log("Mobile number is invalid");
        return {'InvalidMobile': true}
      }
      else {
        this.partyMobile = mobile.replace(/\(+/g, '').replace(/\)+/g, '').replace(/\s+/g, '').replace(/-+/g, '');
      }
    }
    return null
  }

  validateEmail(control: FormControl) {
    let regex = /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@([a-z0-9!#$%&'*+/=?^_`{|}~-]+(\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)+|\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])$/;
    let email: string = control.value;
    if (email) {
      console.log(email);
      if (!regex.test(email.toLowerCase())) {
        console.log("Email is invalid");
        return {'InvalidEmail': true}
      }
    }
    return null
  }

  subscribeSignUpForm() {
    this.signUpForm.statusChanges.subscribe(
      (status) => console.log(status)
    );
    this.signUpForm.valueChanges.subscribe(
      (value) => {
        console.log(value)
      }
    );
  }

  validatePhysicalAddress(control: FormControl): Observable<{ [s: string]: boolean }> {
    this.addressFormArray[0].spinner = true;
    return this.validateAddress(control, this.ADDRESS_PHYSICAL);
  }

  validateMailingAddress(control: FormControl): Observable<{ [s: string]: boolean }> {
    this.addressFormArray[1].spinner = true;
    return this.validateAddress(control, this.ADDRESS_MAILING);
  }

  validateAddress(control: FormControl, addressType: string): Observable<{ [s: string]: boolean }> {
    let tempRef = this;
    let address = control.value;
    console.log(control);
    return new Observable<{ [s: string]: boolean }>((response) => {

      /* this should be the place to filter input given by the user */
      if (address) {
        address = address.trim();

        if (/\d+$/.test(address)) {
          address = address + ' Australia';
        }
      }
      tempRef.googleAutocompleteService.getPlacePredictions(
        {componentRestrictions: {country: 'AU'}, input: address}, function (predictions, status) {
          console.log(status);
          if (status == tempRef.googleService.maps.places.PlacesServiceStatus.OK) {
            console.log(predictions);
            predictions.forEach((value) => {
              let searchResult = value.description.replace(/\,+/g, '').trim().toLowerCase();
              let searchAddress = address.replace(/\,+/g, '').trim().toLowerCase();

              console.log(searchResult);
              console.log(searchAddress);
              console.log(value);
              console.log(value.terms);
              if ((searchResult === searchAddress && value.terms.length == 6
                && /^\d+$/.test(value.terms[0].value) && /^\d+$/.test(value.terms[4].value)
                && value.terms[5].value.toLowerCase() === 'australia')
                ||
                (searchResult === searchAddress && value.terms.length == 5
                && /^\d+.+$/.test(value.terms[0].value) && /^\d+$/.test(value.terms[3].value)
                && value.terms[4].value.toLowerCase() === 'australia')) {
                console.log("address is valid from google results");
                response.next(null);
                response.complete();
                tempRef.setAddress(value, addressType);
              }

            });
            response.next({'NoAddressFound': true});
            response.complete();
          }
          if (status == tempRef.googleService.maps.places.PlacesServiceStatus.ZERO_RESULTS) {
            response.next({'NoAddressFound': true});
            response.complete();
          }
          if (status == tempRef.googleService.maps.places.PlacesServiceStatus.INVALID_REQUEST) {
            response.next({'InvalidInput': true});
            response.complete();
          }
          if (status == tempRef.googleService.maps.places.PlacesServiceStatus.OVER_QUERY_LIMIT) {
            response.next({'QueryLimitCrossed': true});
            response.complete();
          }
          if (status == tempRef.googleService.maps.places.PlacesServiceStatus.REQUEST_DENIED) {
            response.next({'RequestDenied': true});
            response.complete();
          }
          if (status == tempRef.googleService.maps.places.PlacesServiceStatus.UNKNOWN_ERROR) {
            response.next({'UnknownError': true});
            response.complete();
          }
        });
    });
  }


  initAddressesSubscription(address) {
    let tempRef = this;
    if (!address.addressSubscription || address.addressSubscription.closed) {
      console.log(address.addressSubscription);
      address.addressSubscription = this.signUpForm.get('userData.' + address.name).valueChanges.subscribe(
        (value) => {
          console.log(value);
          if (value) {
            this.googleAutocompleteService.getPlacePredictions(
              {componentRestrictions: {country: 'au'}, input: value}, function (predictions, status) {
                if (status == 'OK') {
                  address.autoAddressesSuggesstion = predictions;
                  if (tempRef.signUpForm.get('userData.' + address.name).value
                    === predictions[0].description && predictions.length === 1) {
                    address.autoAddressesSuggesstion.length = 0;
                  }
                }
                else {
                  address.autoAddressesSuggesstion.length = 0;
                }
              });

          }
        }
      );
    }

  }

  cancelAddressesSubscription(address) {
    if (address.addressSubscription && !address.addressSubscription.closed) {
      address.addressSubscription.unsubscribe();
    }
  }

  onAddressFocus($event, address) {
    console.log("on address focus");
    address.spinner = true;
    if (address.type == this.ADDRESS_PHYSICAL) {
      this.addressFormArray[1].spinner = false;
    }
    if (address.type == this.ADDRESS_MAILING) {
      this.addressFormArray[0].spinner = false;
    }
    this.changeDetectorRef.detectChanges();
  }

  onAddressBlur($event, address) {
    address.spinner = false;
    this.changeDetectorRef.detectChanges();
  }

  onAddressSelected(address, addressSelected) {
    this.cancelAddressesSubscription(address);
    //address.autoAddressesSuggesstion.length = 0;
    this.signUpForm.get('userData.' + address.name).setValue(addressSelected.description);
    let ref = this;
    setTimeout(function () {
      ref.changeDetectorRef.detectChanges();
    }, 600);
  }

  onAddressKeyUp($event, address) {
    console.log($event);
  }

  getDetailFormStatus() {
    return this.detailFormActive;
  }

  onAddressHover($event) {
    console.log($event);
    $event.target.focus();
  }

  setAddress(value: any, type: string) {
    let tempAddress: string = null;
    if (value.terms.length === 6) {
      tempAddress = value.terms[0].value + " " + value.terms[1].value + ":" +
        value.terms[2].value + ":" + value.terms[3].value + ":" +
        value.terms[4].value + ":" + value.terms[5].value;
    }
    else if (value.terms.length === 5) {
      tempAddress = value.terms[0].value + ":" + value.terms[1].value + ":" +
        value.terms[2].value + ":" + value.terms[3].value + ":" + value.terms[4].value;
    }

    if (type == this.ADDRESS_PHYSICAL) {
      this.addressFormArray[0].spinner = false;
      this.partyPhysicalAddress = tempAddress;
    }
    else if (type == this.ADDRESS_MAILING) {
      this.partyMailingAddress = tempAddress;
      this.addressFormArray[1].spinner = false;
    }
    this.changeDetectorRef.detectChanges();
  }

  open(content) {
    this.ngbModal.open(content).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  saveUserComment() {
    if (this.userCommentInput && this.userCommentInput.replace(/^\s+$/, '') != "") {
      let comment = {
        "comment": this.userCommentInput,
        "staff" : "/staffs/" + this.paramsReceived,
        "party" : "/parties/" + this.paramsReceived,
      };
      this.serverService.saveUserComment(comment)
        .subscribe((response)=>{
            console.log(response);
            this.ngbModal.open("Comments Succesfull saved");
            this.getUserComments();
        })
    }
    else {
      this.ngbModal.open("Please enter a valid comment");
    }
  }
  dontSaveUserComment() {
    this.userCommentInput = "";
    this.newComment = false;
  }

  ngOnDestroy() {
    this.partyServiceSubscription.unsubscribe();
    this.partyService.announcePartyIdBeforeDestroy(this.paramsReceived);
  }
}



