import {AfterViewInit, Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {ServerService} from "../../../../services/server-service";
import {PartyService} from "../../../../services/party.service";
import {Subscription} from "rxjs/Subscription";
import {GridOptions} from "ag-grid";
import {NavBarService} from "../../../../services/nav-bar.service";

@Component({
  selector: 'app-party-bills',
  templateUrl: './party-bills.component.html',
  styleUrls: ['./party-bills.component.scss']
})
export class PartyBillsComponent implements OnInit {

  paramsReceived:number = null;
  partyServiceSubscription:Subscription;
  gridOptions: GridOptions;
  rowData: any[];
  navBarSubscription: Subscription;

  constructor(private activatedRoute: ActivatedRoute,
              private serverService:ServerService,
              private partyService:PartyService,
              private navBarService: NavBarService) { }

  ngOnInit() {
    this.partyServiceSubscription = this.partyService.partyIdSourceObservable.subscribe((id) => {

      console.log(id);
      this.paramsReceived = id;
      this.serverService.getRequestByPartyId(this.paramsReceived, '/bills')
        .subscribe(
          (partyBills:any)=>{
            console.log(partyBills);
            this.rowData = partyBills._embedded.bills;
          },
          (error)=>{console.log(error)});
    }, (error) => console.log(error));
    this.gridOptions = {
      context: {
        componentParent: this
      },
      rowData: this.rowData,
      columnDefs: this.columnDefs
    }

    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      console.log("sizeColumnsToFit");
      this.gridOptions.api.sizeColumnsToFit();
    });
  }

  columnDefs = [
    {
      headerName: "Bill id",
      field: 'bill_id',
      cellClass: 'first-column'
    },
    {
      headerName: "Job id",
      field: "job_id",
    },
    {
      headerName: "Bill date",
      field: "bill_date",
    },
    {
      headerName: "Due date",
      field: "due_date",
    },
    {
      headerName: "Total amount",
      field: "total_amount",
    }
  ];
  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event.data);
    let href = $event.data._links.self.href;
    let index = (href.lastIndexOf('/') + 1);
    let id = +(href.substr(index));
    console.log(id);
    // this.router.navigate(['party', id, 'invoices'])
    //   .then((value) => {
    //     console.log(value);
    //   }).catch((error) => {
    //   console.log(error)
    // });
  }

  onCellDoubleClicked($event) {
    console.log($event.data._links.self);
  }

  ngAfterViewInit() {
    this.gridOptions.api.sizeColumnsToFit();
  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
    this.partyServiceSubscription.unsubscribe();
    this.partyService.announcePartyIdBeforeDestroy(this.paramsReceived);
  }

}



